.hero {
    display: flex;
    align-items: center;
    padding: 0 100px;
    height: 90vh;
}

.hero .hero-left {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.hero .hero-left .hero-left-body h1 {
    font-size: 17px;
}

.hero .hero-left .hero-left-body p {
    font-size: 60px;
    font-weight: 700;
    color: #60606060;
}

.hero .hero-left a {
    background-color: orange;
    padding: 15px 20px;
    color: #000;
    border-radius: 22px;
    border: none;
    outline: none;
    font-weight: 700;
    font-size: 18px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    text-decoration: none;
}

.hero .hero-left a:hover {
    transform: scale(1.04);
    opacity: 0.7;
    transition: 0.2s;
}

.hero .hero-left a img {
    width: 36px;
    height: 36px;
    object-fit: cover;
}

.hero .hero-right {
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero .hero-right img {
    margin-left: 300px;
}