.recipe {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    /* margin-bottom: 100px; */
    margin-top: 40px;
}

.recipe .recipe-left {
    display: flex;
    align-items: center;
    justify-content: center;
}

.recipe .recipe-left img {
    object-fit: cover;
    max-width: 500px;
    max-height: 500px;
}

.recipe .recipe-right {
    margin-left: 140px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
    justify-content: center;
}

.recipe .recipe-right h1 {
    font-size: 60px;
}

.recipe .recipe-right span {
    color: green;
    font-weight: 700;
}

.recipe .recipe-right button {
    background-color: #000;
    border-radius: 22px;
    padding: 6px 15px;
    color: #fff;
    border: none;
    font-size: 12px;
    margin: 10px;
    cursor: pointer;
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.recipe .recipe-right button img {
    width: 30px;
    height: 30px;
    object-fit: cover;
}

.recipe .recipe-right button:hover {
    opacity: 0.7;
    transform: scale(1.03);
    transition: 0.3s;
}

.container {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

.side {
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 20px;
    background-color: whitesmoke;
    gap: 20px;
    margin: 20px 0;
    border-radius: 10px;
}

.side h3 {
    color: orange;
}

.side ul {
    display: flex;
    flex-direction: column;
    gap: 10px;
    list-style: none;
}

.side ul a {
    text-decoration: none;
    color: black;
    padding: 3px 6px;
    border-radius: 4px;
}

.side ul a:hover {
    background-color: orange;
    transition: 0.3s;
}

.active {
    background-color: orange;
}