.footer {
    margin-top: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    padding: 20px;
    background-color: whitesmoke;
}

.footer .footer-icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.footer .footer-icon p {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.footer .footer-icon img {
    object-fit: cover;
    max-width: 20px;
    max-height: 20px;
}

.footer .footer-icon .main {
    object-fit: cover;
    max-width: 50px;
    max-height: 50px;
}

.footer .footer-links {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 200px;
}


.footer .footer-social {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
}

.footer .footer-links li {
    border-radius: 5px;
    padding: 4px 8px;
    cursor: pointer;
}


.footer .footer-links li:hover {
    background-color: orange;
    transform: scale(1.01);
    transition: 0.3s;
}


.fa {
    padding: 15px;
    text-decoration: none;
    border-radius: 40px;
}

.fa:hover {
    opacity: 0.7;
}

.fa-facebook {
    background: whitesmoke;
    border: 1px solid orange;
    color: orange;
    padding: 15px 18px;
    border-radius: 150x;
}

.fa-instagram {
    background: whitesmoke;
    border: 1px solid orange;
    color: orange;
}

.fa-linkedin {
    background: whitesmoke;
    border: 1px solid orange;
    color: orange;
}

.fa-facebook:hover,
.fa-instagram:hover,
.fa-linkedin:hover {
    border: none;
    background-color: orange;
    color: #fff;
    transition: 0.3s;
}