.popular {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 150px;
}

.popular .popular-title hr {
    border: none;
    height: 3px;
    background-color: black;
    border-radius: 10px;
    width: 70%;
    margin: 0px 70px;
}

.popular .popular-recipes {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 100px;
}