.newsletter {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0 140px;
    margin-bottom: 150px;
    gap: 20px;
}

.newsletter .newsletter-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.newsletter .newsletter-form input {
    padding: 10px 20px;
    outline: none;
    border: 1px solid #17171717;
    width: 400px;
    border-radius: 33px;
}

.newsletter .newsletter-form button {
    outline: none;
    border: none;
    background-color: #000;
    color: #fff;
    padding: 8px 16px;
    border-radius: 22px;
    cursor: pointer;
    margin-left: -30px;
}

.newsletter .newsletter-form button:hover {
    transform: scale(1.04);
    opacity: 0.7;
    transition: 0.3s;
}