.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    cursor: pointer;
    text-decoration: none;
    color: black
}

.item-props {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.item img {
    max-width: 300px;
    max-height: 300px;
    object-fit: cover;
}

.item img:hover {
    transform: scale(1.04);
    transition: 0.3s;
}