.cart {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 30px;
    margin-bottom: 50px;
}

.cart .cart-titles,
.cart .cart-body {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    row-gap: 10px;
    padding: 10px;
    margin-right: -40px;
}

.cart .cart-body img {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    object-fit: cover;
}

.cart .cart-body .remove {
    cursor: pointer;
}

.cart .cart-titles p,
.cart .cart-body p {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

.cart .cart-body button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    border: 1px solid #000;
    outline: none;
    border-radius: 22px;
    background-color: #fff;
    cursor: pointer;
}

.cart .cart-body button:hover,
.cart-bottoms .cart-promocode button:hover {
    opacity: 0.7;
    transform: scale(1.05);
    transition: 0.3s;
}

.cart .alert {
    width: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-self: center;
    gap: 10px;
    padding: 20px 10px;
    background-color: rgb(255, 255, 180);
    color: rgb(95, 95, 0);
    border: 1px solid rgb(255, 255, 0);
    border-radius: 6px;
}

.cart-bottoms {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 50px;
}

.cart-bottoms .cart-totals {
    display: flex;
    flex-direction: column;
    width: 500px;
}

.cart-bottoms .cart-totals-body div {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 20px 0;
}

.cart-bottoms .cart-totals button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 14px;
    border-radius: 22px;
    border: none;
    background-color: #000;
    color: #fff;
    cursor: pointer;
    width: 200px;
}

.cart-bottoms .cart-totals button:hover {
    transform: scale(1.05);
    opacity: 0.7;
    transition: 0.3s;
}

.cart-bottoms .cart-form {
    display: flex;
    flex-direction: column;
    align-items: flex start;
    justify-content: center;
    width: 500px;
    gap: 10px;
}

.cart-bottoms .cart-form {
    display: flex;
    justify-content: center;

}

.cart-bottoms .cart-form input {
    padding: 10px 16px;
    outline: none;
    border: 1px solid #20202020;
    width: 400px;
    border-radius: 22px;
    background-color: whitesmoke;
}

.cart-bottoms .cart-form button {
    width: 100px;
    border: none;
    outline: none;
    border-radius: 22px;
    background-color: #000;
    color: #fff;
    cursor: pointer;
    padding: 13px;
    width: 200px;
}