.banner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    padding: 0 120px;
    background: linear-gradient(180deg, orange, white 80%);
    margin-bottom: 50px;
}
.banner .banner-left {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
}

.banner .banner-left p {
    font-size: 40px;
}

.banner .banner-left ul {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
}

.banner .banner-right {
    display: flex;
    align-items: center;
    justify-content: center;
}