.nav {
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: whitesmoke;
}

.nav .nav-title,
.nav .nav-body {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.nav .nav-title {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3px;
    margin-left: 30px;
}

.nav .nav-body {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 700px;
    gap: 20px;
}

.nav .nav-title img {
    object-fit: cover;
    max-width: 50px;
    max-height: 50px;
}

.nav .nav-body img {
    object-fit: cover;
    max-width: 40px;
    max-height: 40px;
}

.nav .nav-body .nav-cart {
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav .nav-body .nav-cart p {
    margin-top: -38px;
    margin-left: -38px;
    background-color: orange;
    padding: 0 6px;
    font-size: 12px;
    border-radius: 33px;
    color: #ffffff;
}

.nav .nav-body a {
    color: #000;
    text-decoration: none;
}

.nav .nav-body a,
.nav .nav-body div {
    padding: 6px;
    border-radius: 5px;
}

.nav .nav-body a:hover {
    background-color: orange;
    transition: 0.3s;
}

.active {
    background-color: orange;
}

.nav .nav-body .nav-cart:hover {
    background-color: whitesmoke;
}