.menu-items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    margin-bottom: 120px;
}

.menu-items .menu-items-title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.menu-items .menu-items-title h1 {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}


.menu-items .menu-items-title h1 img {
    width: 40px;
    height: 40px;
    object-fit: cover;
}


.menu-items .menu-items-title hr {
    border-radius: 22px;
    border: none;
    height: 3px;
    background-color: #000;
    width: 20%;
    margin: 0 80px;
}

.menu-items .menu-items-recipes {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.menu-items .menu-items-recipes img {
    object-fit: cover;
}

.menu-items .menu-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.menu-items .menu-form input {
    padding: 10px 20px;
    border: 1px solid #17171717;
    border-radius: 20px;
    width: 500px;
    outline: none;
    background-color: whitesmoke;
}

.menu-items .menu-form input:focus {
    border: 4px solid rgb(255, 223, 164);
    transition: 0.2s;
}